.weatherForm {
  font-size: 16px;
  position: relative;
  width: 100%;
}
.weatherForm form {
  align-items: center;
  display: flex;
  font-size: 16px;
  justify-content: space-between;
  text-shadow: 0 1px 1px #7a2929;
}
@media only screen and (max-width: 768px) {
  .weatherForm form {
    display: block;
  }
}
.weatherForm input,
.weatherForm button {
  border: none;
  border-radius: 0;
  font-size: 16px;
}
.weatherForm input[type=text] {
  background: rgba(210, 74, 74, 0.5);
  color: #fff;
  padding: 10px 10px;
  transition: box-shadow .3s linear 0s;
  width: 68%;
}
.weatherForm input[type=text]::placeholder {
  color: #f2f2f2;
  font-family: 'Roboto', sans-serif;
}
@media only screen and (max-width: 768px) {
  .weatherForm input[type=text] {
    margin: 0 0 20px 0;
    width: 100%;
  }
}
.weatherForm button {
  background: #F45050;
  color: #fff;
  cursor: pointer;
  margin: 0 15px;
  padding: 10px 19px;
  text-shadow: 0 1px 1px #7a2929;
  transition: box-shadow .3s linear 0s;
}
.weatherForm input[type=text]:hover,
.weatherForm button:hover {
  box-shadow: 0 0 1px 1px #fff;
}
@media only screen and (max-width: 768px) {
  .weatherForm button {
    margin: 0;
    width: 100%;
  }
}
.weatherForm p {
  font-family: 'Bangers', cursive;
  letter-spacing: 1px;
  text-align: center;
  text-shadow: 0 1px 1px #000;
}
@media only screen and (max-width: 768px) {
  .weatherForm p {
    margin: 15px 0 0 0;
  }
  .resetButton {
    margin: 10px 0 0 0;
  }
}
.resetButton p {
  display: inline;
  font-size: 18px;
  margin: 0 10px 0 0;
}
.resetButton button {
  background: none;
  border: 1px solid #fff;
  box-shadow: 0 0 1px 1px #4c4c4c;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  margin: 15px 0 0 0;
  outline: none;
  padding: 10px 19px;
  text-shadow: 1px 1px 1px #000;
}